import React from 'react';
import WithChat from './hocs/WithChat';
import WithAdmin from './hocs/WithAdmin';
import {HashRouter, BrowserRouter as Router, Route} from "react-router-dom";

function App() {
  return (
    <HashRouter basename='/'>
      <Router>
        <div className="App">
          <Route exact path="/semear" component={Home} />
          <Route path="/semear/admin" component={Admin} />
        </div>
      </Router>
    </HashRouter>
  );
}

const Home = () => <WithChat />;

const Admin = () => <WithAdmin />;

export default App;
